<template>
  <div>
    <div >
      <el-tabs
        v-model="activeIndex"
        type="border-card"
        closable
        v-if="openTab.length"
        @tab-click='tabClick'
        @tab-remove='tabRemove'
        shadow="always"
        >
        <el-tab-pane
          :key="item.name"
          v-for="(item, index) in openTab"
          :label="item.title"
          :name="item.route"
         >
        </el-tab-pane>
        <div class="content-show">
          <router-view></router-view>
        </div>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
    }
  },
  methods:{
    //tab
    tabClick(tab){
      //console.log("tab",tab);
      //console.log('active',this.activeIndex);
      this.$router.push({path: this.activeIndex});
    },
    //tab
    tabRemove(targetName){
      //console.log("tabRemove",targetName);
      //
      if(targetName == '/'){
        return
      }
      this.$store.commit('delete_tabs', targetName);
      if (this.activeIndex === targetName) {
        // 
        if (this.openTab && this.openTab.length >= 1) {
          //console.log('=============',this.openTab[this.openTab.length-1].route)
          this.$store.commit('set_active_index', this.openTab[this.openTab.length-1].route);
          this.$router.push({path: this.activeIndex});
        } else {
          this.$router.push({path: '/'});
        }
      }
    }
  },
  mounted () {
    // tabtabs
    // store
    // store
    if (this.$route.path !== '/' && this.$route.path !== '/news') {
      //console.log('1');
      this.$store.commit('add_tabs', {route: '/news' , name: 'contentManage.news',title:""});
      this.$store.commit('add_tabs', {route: this.$route.path , name: this.$route.name,title:this.$route.meta.title });
      this.$store.commit('set_active_index', this.$route.path);
    } else {
      //console.log('2');
      this.$store.commit('add_tabs', {route: '/news', name: 'contentManage.news',title:""});
      this.$store.commit('set_active_index', '/news');
      this.$router.push('/');
    }

  },
  computed: {
    openTab () {
      return this.$store.state.openTab;
    },
    activeIndex:{
      get () {
        return this.$store.state.activeIndex;
      },
      set (val) {
        this.$store.commit('set_active_index', val);
      }
    }
  },
  watch:{
    '$route'(to,from){
        //
        // active
        //
        let flag = false;
        for(let item of this.openTab){
          //console.log("item.name",item.name)
          //console.log("t0.name",to.name)

          if(item.name === to.name){
            //console.log('to.path',to.path);
            this.$store.commit('set_active_index',to.path)
            flag = true;
            break;
          }
        }

        if(!flag){
          //console.log('to.path',to.path);
          this.$store.commit('add_tabs', {route: to.path, name: to.name,title:to.meta.title});
          this.$store.commit('set_active_index', to.path);
        }

    }
  }
};
</script>

<style lang="scss">
.content-show{
  flex: 1;
  max-height:700px;
  overflow-y:auto;
}
</style>